<template>
  <div id="activityApp">
    <nav class="navbar is-white topNav">
      <div class="container">
        <div class="navbar-brand">
          <h1>{{ fullAppName }}</h1>
          <!-- <h1>{{ watchedAppName }}</h1> -->
        </div>
      </div>
    </nav>
    <!-- The Navbar -->
    <TheNavbar @filterSelected="setFilter"/>
    <!-- ## The Navbar -->
    <section class="container">
      <div class="columns">
        <div class="column is-3">
          <!-- Activity Form -->
              <ActivityCreate :categories="categories"/>
          <!-- ## Activity Form -->
        </div>
        <div class="column is-9">
          <div class="box content" 
              :class="{ fetching : isFetching, 'has-error' : error }">
            <div v-if="error">
              {{ error }}
            </div>
            <div v-else>
              <div v-if="isFetching">
                Loading...
              </div>
              <!-- Activity Item -->
              <div v-if="isDataLoaded">
              <ActivityItem 
                v-for="activity in filterActivities"
                :key="activity.id"
                :activity="activity"
                :categories="categories"
                />
                </div>
              <!-- ## Activity Item -->
            </div>
            <div v-if="!isFetching">
              <div class="activity-length">Currently {{ activityLength }} activities</div>
              <div class="activity-motivation">{{ activityMotivation }}</div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Vue from "vue"
import store from "@/store"
import ActivityItem from "@/components/ActivityItem"
import ActivityCreate from "@/components/ActivityCreate"
import TheNavbar from "@/components/TheNavbar"
// import { fetchActivities, fetchCategories, fetchUser, deleteActivityAPI } from "@/api"
import fakeApi from "@/lib/fakeApi"
export default {
  name: 'App',
  components : { ActivityItem, ActivityCreate, TheNavbar },
  data () {
    const { state : { activities, categories } } = store
    return {
      creator : "Yunus Cecen",
      appName : "Activity Planner",
      isFetching : false,
      error : null,
      watchedAppName : "Activity Planner by Yunus Cecen",
      items: {1: {name: 'Filip'}, 2: {name: 'John'}},
      user: {},
      activities,
      categories,
      filter : 'all'
    }
  },
  computed : {
    
    filterActivities () {

      let filteredActivities = {}
      let condition 
      if(this.filter === "all"){
        return this.activities
      }

      if(this.filter === "inprogress"){
        condition = (value) => value > 0 && value < 100
        // condition = function(value){
        //   if(value > 0 && value < 100){
        //     return true
        //   }else {
        //     return false
        //   }
        // } 
      }else if(this.filter === "finished"){
        condition = (value) => value === 100
      }else {
        condition = (value) => value === 0
      }

      filteredActivities = Object.values(this.activities).filter(activity => {
          return condition(activity.progress)
      })
      return filteredActivities; 
    },
    fullAppName(){
      return this.appName + " by " + this.creator;
    },
    activityLength() {
      return Object.keys(this.activities).length
    },
    activityMotivation () {
      if(this.activityLength && this.activityLength < 5){
        return "Nice to see some activities (:"
      }else if(this.activityLength >= 5){
        return "So many activities! Good Job!"
      }else {
        return "No activities, so sad :("
      }
    },
    activitiesLength () {
      return Object.keys(this.activities).length
    },
    categoriesLength () {
      return Object.keys(this.categories).length
    },
    isDataLoaded(){
      return this.activitiesLength && this.categoriesLength
    }
  },
  created() {
    this.isFetching = true
    store.fetchActivities().then((activities) => {
      this.isFetching = false
    }).catch(err => {
      this.error = err
      this.isFetching = false
    })

    this.user = store.fetchUser()
    store.fetchCategories().then(categories => {})
  },
  methods : {
    setFilter(filterOption) {
      this.filter = filterOption
    }
  }
}
</script>

